nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 1rem 3rem;
    box-sizing: border-box;
    width: 100vw;
    position: fixed;
    background-color: white;
    top: 0;
}

a {
    text-decoration: none;
    color: black;
}

img {
    height: 50px;
    width: 50px;
}

ul {
    display: flex;
}

li {
    list-style: none;
    margin-left: 1rem;
}

span {
    margin-left: 1rem;
    line-height: 50px;
}